<template>
<div class="bell">
  <div class="buyheader">
    <h1>加盟合作</h1>
  </div>
  <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
    <el-breadcrumb-item>加盟合作</el-breadcrumb-item>
  </el-breadcrumb>
  <ul class="cooperation">
    <li>
      <a href="#/bellUS/0">药店加盟</a>
    </li>
    <li>
      <a href="#/bellUS/1">诊所加盟</a>
    </li>
    <li>
      <a href="#/bellUS/2">医院加盟</a>
    </li>
    <li>
      <a href="#/bellUS/3">厂家合作</a>
    </li>
  </ul>
  <div class="charts">
    <h3>入驻分析</h3>
    <ve-histogram :data="chartData" :settings="chartSettings"></ve-histogram>
    <h3>入驻类型</h3>
    <ve-ring :data="chartData1" :settings="chartSettings1"></ve-ring>
    <h3>收入分析</h3>
    <ve-line :data="chartData2" :settings="chartSettings2"></ve-line>
  </div>
  
  
</div>
</template>

<script>

export default {
  data() {
    return {};
  },
  methods: {},
  created: function() {
    this.chartData = {
      columns: ["日期", "药店", "诊所", "医院"],
      rows: [
        { 日期: "1月1日", 药店: 200, 诊所: 240, 医院: 100 },
        { 日期: "1月2日", 药店: 248, 诊所: 138, 医院: 100 },
        { 日期: "1月3日", 药店: 324, 诊所: 267, 医院: 100 },
        { 日期: "1月4日", 药店: 100, 诊所: 189, 医院: 100 }
      ]
    };
    this.chartSettings = {
      axisSite: { right: ["占比"] },
      yAxisType: ["KMB", "percent"],
      yAxisName: ["数值", "比率"]
    };
    this.chartData1 = {
      columns: ["类别", "数量"],
      rows: [
        { 类别: "药店", 数量: 123 },
        { 类别: "诊所", 数量: 239 },
        { 类别: "医院", 数量: 111 }
      ]
    };
    this.chartSettings1 = {
      roseType: "radius"
    };
    this.chartData2 = {
      columns: ["日期", "成本", "利润", "占比", "其他"],
      rows: [
        { 成本: 1523, 日期: "1月1日", 利润: 1523, 占比: 0.12, 其他: 100 },
        { 成本: 1223, 日期: "1月2日", 利润: 1523, 占比: 0.345, 其他: 100 },
        { 成本: 2123, 日期: "1月3日", 利润: 1523, 占比: 0.7, 其他: 100 },
        { 成本: 4123, 日期: "1月4日", 利润: 1523, 占比: 0.31, 其他: 100 },
        { 成本: 3123, 日期: "1月5日", 利润: 1523, 占比: 0.12, 其他: 100 },
        { 成本: 7123, 日期: "1月6日", 利润: 1523, 占比: 0.65, 其他: 100 }
      ]
    };
    this.chartSettings2 = {
      metrics: ["成本", "利润"],
      dimension: ["日期"]
    };
  },
  components: {}
};
</script>

<style lang="scss" scoped>
.cooperation {
  margin: 0 15px;
  li {
    width: 49%;
    float: left;
    margin-left: 1%;
    margin-bottom: 1%;
  }
  a {
    color: #461b93;
    padding: 40px;
    font-size: 15px;
    border-radius: 2px;
    display: block;
    text-align: center;
    background: #eee;
    letter-spacing: 1px;
  }
}

.cooperation:after {
  content: "\0020";
  display: block;
  height: 0;
  clear: both;
}

.charts {
  padding: 15px;
  h3 {
    background: #461b93;
    color:#fff;
    padding: 8px;
    font-size: 15px;
    margin-bottom: 15px;
  }
}
</style>



